html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%);
  color: #1a1b15;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-size: 2.6rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  font-weight: 700;
}

.subtitulo {
  text-align: left;
  font-weight: 700;
  margin: 5rem 0 0 2rem;
  font-size: 2.5rem;
}

.descripcion-pagina {
  text-align: center;
  margin-bottom: 5rem;
}

.contenedor-app {
  height: 95vh;
  width: 95vw;
  margin: auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .imagen {
    background-size: cover;
    background-position: center center;
    background-image: url(../img/login_image.jpg);
    height: 95vh;
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 1rem;
  overflow: auto;
  background-color: #ffffff;
}

.barra {
  margin-bottom: 7rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #ffffff;
  padding: 1.5rem 4rem;
  color: #1a1b15;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.barra-componentes {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-componentes {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
}
.barra-componentes .boton {
  margin: 0 0 1rem 0;
  padding: 1.5rem 3rem;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-componentes {
  display: grid;
  gap: 2rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.listado-componentes .componente {
  border: 1px solid #0369fc;
  border-radius: 1rem;
  padding: 2rem;
  transition-property: transform;
  transition-duration: 300ms;
  margin: 0.5rem;
  text-align: center;
}
.listado-componentes .componente.seleccionado {
  background-color: #0369fc;
}
.listado-componentes .componente.seleccionado p {
  color: #ffffff;
}
@media (min-width: 768px) {
  .listado-componentes .componente {
    text-align: center;
  }
}
.listado-componentes .componente:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.listado-componentes .componente p {
  color: #1a1b15;
  margin: 0 0 1.25rem;
}
.listado-componentes .componente .categoria-componente {
  color: #0369fc;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
}

.d-block {
  display: block;
}

.d-none {
  display: none;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0369fc;
  font-weight: 700;
  font-size: 2rem;
}

.contenedor-componente {
  padding: 3rem 0;
  border-bottom: 1px solid #636262;
  display: flex;
  align-items: center;
}
.contenedor-componente input {
  width: 5rem;
  height: 2.5rem;
}
.contenedor-componente p:first-of-type {
  color: #0369fc;
  font-weight: 700;
}
.contenedor-componente p {
  margin: 0 2rem 0;
}
.contenedor-componente:last-of-type {
  margin-bottom: 5rem;
}

.formulario {
  width: 100%;
  background-color: #ffffff;
}
.formulario .campo label {
  display: block;
  margin: 0.5rem 0;
}
.formulario .campo input {
  display: block;
  min-width: 100%;
  border: 1px solid #1a1b15;
  border-radius: 5px;
  margin: 0 0 1.5rem 0;
  padding: 1rem;
}
.formulario .campo input:disabled {
  background-color: #afafaf;
  color: #1a1b15;
  font-weight: 700;
  cursor: not-allowed;
}
@media (min-width: 768px) {
  .formulario .campo input {
    width: 100%;
  }
}

.btn-green {
  background-color: #007e33;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
