  }
}
.btn-yellow:hover {
  background-color: #cc6d00;
  cursor: pointer;
}

.btn-red {
  background-color: #cc0000;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
