  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .btn-red {
    width: auto;
  }
}
.btn-red:hover {
  background-color: #990000;
  cursor: pointer;
}

.btn-lightBlue {
  background-color: #33b5e5;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  padding: 1rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .btn-lightBlue {
    width: auto;
  }
}
.btn-lightBlue:hover {
  background-color: #1a9bcb;
  cursor: pointer;
}

.boton {
  background-color: #0369fc;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 3rem 0;
  padding: 1.5rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0254ca;
  cursor: pointer;
}

@media (min-width: 768px) {
  .acciones-auth {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .acciones-auth a {
    color: #0369fc;
    margin: 0;
    text-decoration: none;
    display: block;
    text-align: center;
  }
}
.acciones-auth .eliminar input {
  margin: 0;
}

.alerta {
  padding: 1.25rem;
  color: #ffffff;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
    gap: 1rem;
  }
}
.tabs button {
  background-color: #0369fc;
  color: #ffffff;
  border: none;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0254ca;
}
.tabs button.actual {
  background-color: #ffffff;
  color: #0369fc;
  border: 1px solid #0369fc;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .paginacion button {
    margin: 0;
  }
}
@media (min-width: 375px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  .paginacion button {
    padding: 1rem;
  }
}

.ocultar {
  visibility: hidden;
}

.filtroInput {
  display: block;
  min-width: 100%;
  border: 1px solid #1a1b15;
  border-radius: 5px;
  margin: 3rem 0 3rem 0;
  padding: 1rem;
}
.filtroInput:focus {
  border: 1px solid #0369fc;
}

.verde {
  color: #329f00;
}

.rojo {
  color: #cb0000;
}

.amarillo {
  color: #ff8800;
}

.azul {
  color: #0369fc;
}

.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100%;
  }
}
.dashboard .contenido {
  padding: 3rem;
}
.dashboard .contenido .subtitulo {
  font-size: 2.5rem;
  margin: 5rem 0 2rem 0;
}
.dashboard .contenido input[type=date] {
  background-color: #ffffff;
  color: #1a1b15;
  padding: 1.5rem;
  border: none;
  outline: none;
  border-radius: 0.5rem;
}

.sidebar {
  background-color: #0369fc;
  padding: 1.5rem;
}
.sidebar h2 {
  color: #ffffff;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #ffffff;
  display: block;
  margin-bottom: 1.5rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-weight: bolder;
  font-size: 1.8rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #023f97;
  border-radius: 0.5rem;
}

.principal {
  flex: 1;
}
.principal .barra {
  background-color: #ffffff;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  margin-bottom: 0;
}
.principal .barra p span {
  font-weight: 700;
  color: #1a1b15;
}
.principal .boton {
  background-color: #0369fc;
  color: #ffffff;
  font-size: 1.8rem;
  border-radius: 0.5rem;
  padding: 1.5rem;
  display: initial;
}
.principal .boton:hover {
  background-color: #023f97;
}

.tabla {
  background-color: #ffffff;
  width: 100%;
  border-radius: 1rem;
  margin-top: 2rem;
  padding: 1rem;
  text-align: center;
  box-shadow: 3px 6px 13px -1px rgba(0, 0, 0, 0.27);
  -webkit-box-shadow: 3px 6px 13px -1px rgba(0, 0, 0, 0.27);
  -moz-box-shadow: 3px 6px 13px -1px rgba(0, 0, 0, 0.27);
}

.tabla-encabezado tr {
  color: #1a1b15;
  text-align: center;
  font-weight: bold;
}

.tabla th,
.tabla td {
  padding: 12px 15px;
}
.tabla li {
  list-style: none;
